.mood-wrap {
  display: flex;
  margin-left: 20px;
}

.mood-wrap > div {
  margin-right: 10px;
}

.graph-container {
  width: 300px;
  border-left: 1px solid rgb(218, 218, 218);
}

.graph-container_number {
  width: 300px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgb(207, 207, 207);
  padding-bottom: 10px;
}
.keys {
  text-transform: uppercase;
  font-weight: bold;
  color: #333;
  height: 25px;
  margin: 3px 0;
}

.list {
  padding-bottom: 10px;
  text-align: center;
  height: 30px;
  display: inline-block;
}

.bar {
  margin: 3px 0;
}